<script>
import { mapState, mapActions } from 'vuex'
import Utils from '@/utils/util'
import viewFIle from '@/components/Modal/ViewFile.vue'
import VuetifyMoney from '../VuetifyMoney.vue'
export default {
  name: 'HCCombenDetail',
  components: {
    viewFIle, VuetifyMoney
  },
  data () {
    return {
      pta: false,
      os: false,
      checked: true,
      notifications: false,
      sound: true,
      widgets: false,
      alignments: [
        'start',
        'center',
        'end'
      ],
      items: [
        {
          text: 'Ya', value: '1'
        },
        {
          text: 'Tidak', value: '0'
        }
      ],
      outlined: true,
      options: {
        locale: 'id-ID',
        prefix: 'Rp ',
        suffix: '',
        length: 12,
        precision: 0
      }
    }
  },
  inject: {
    theme: {
      default: { isDark: false }
    }
  },
  computed: {
    ...mapState('hccomben', {
      state: (state) => state
    }),
    checkedNST () {
      if (this.state.detail.JenisPengajuan === 'NTS') {
        return true
      }
      return false
    },
    checkedNonNST () {
      if (this.state.detail.JenisPengajuan === 'NORMAL') {
        return true
      }
      return false
    },
    select () {
      if (this.state.action === 'Detail') {
        return true
      }
      return false
    }
  },
  methods: {
    ...mapActions('hccomben', [
      'simpan', 'changePTA', 'changeOS'
    ]),
    ...mapActions('modalFile', [
      'setFile'
    ]),
    toRupiah (nominal) {
      return 'Rp ' + Utils.toRupiahString(nominal)
    },
    toDateTime (tanggal) {
      return Utils.toDateTime(tanggal)
    },
    checkFile (file) {
      if (file) {
        const ext = file.split('.')
        if (ext[4] === 'pdf') {
          return true
        } else {
          return false
        }
      }
      return true
    }
  }
}
</script>
