<template>
  <div class="mt-4">
    <v-card-title>
      <div class="text-h5 font-weight-light">
        Rekomendasi Pinjaman
      </div>
      <JsonExcel v-if="data.data.length > 0" :data="data.data" :fields="json_fields" class="ml-auto">
        <v-btn class="primary"><v-icon color="white">mdi-download</v-icon> Download</v-btn>
      </JsonExcel>
    </v-card-title>
    <v-card class="ma-2">
      <v-card-title>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
          width="100px"
        ></v-text-field>
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="data.data"
        loading-text="Loading... Please wait"
        :loading="data.loading"
        :search="search"
        class="elevation-1"
      >
        <template #[`item.index`]="{ item }">
          {{ data.data.indexOf(item)+1 }}.
        </template>
        <template #[`item.SubmitDate`]="{ item }">
            {{ item.SubmitDate ? toDateStringMount(item.SubmitDate) : item.SubmitDate }}
        </template>
        <template #[`item.firstname`]="{ item }">
          {{ item.firstname }} {{ item.lastname }}
        </template>
        <template #[`item.HC_Recommendations`]="{ item }">
            {{ yesOrNo(item.HC_Recommendations) }}
        </template>
        <template #[`item.DSR`]="{ item }">
            {{ yesOrNo(item.DSR) }}
        </template>
        <template #[`item.SP`]="{ item }">
            {{ yesOrNo(item.SP) }}
        </template>
        <template #[`item.action`]="{ item }">
          <v-btn  icon color="primary" @click="detail(item)">
            <v-icon small>
              mdi-eye
            </v-icon>
          </v-btn>
          <v-btn  icon color="warning" @click="edit(item)">
            <v-icon small>
              mdi-lead-pencil
            </v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </v-card>
    <Detail/>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import store from '@/store'
import Detail from '@/components/HCComben/Detail.vue'
import Utils from '@/utils/util'
import JsonExcel from 'vue-json-excel'
export default {
  name: 'HCComben',
  components: {
    Detail, JsonExcel
  },
  data: () => ({
    search: '',
    headers: [
      {
        sortable: false,
        text: '#',
        value: 'index'
      },
      {
        text: 'Tgl Pengajuan',
        align: 'start',
        sortable: false,
        value: 'SubmitDate'
      },
      { text: 'NIK', value: 'NIK', sortable: false },
      { text: 'Nama', value: 'firstname', sortable: false },
      { text: 'Jabatan', value: 'JobTitle', sortable: false },
      { text: 'Lokasi Kerja', value: 'WorkLocation', sortable: false },
      { text: 'Rekomendasi', value: 'HC_Recommendations', sortable: false },
      { text: 'DSR', value: 'DSR', sortable: false },
      { text: 'SP', value: 'SP', sortable: false },
      { text: 'Action', value: 'action', sortable: false }
    ],
    json_fields: {
      'Tgl Pengajuan': 'SubmitDate',
      NIK: 'NIK',
      Nama: 'firstname',
      Jabatan: 'JobTitle',
      'Lokasi Kerja': 'WorkLocation',
      Rekomendasi: {
        field: 'HC_Recommendations',
        callback: (value) => {
          if (value === 0) {
            return 'Tidak'
          } else if (value === 1) {
            return 'Iya'
          } else {
            return ''
          }
        }
      },
      DSR: {
        field: 'DSR',
        callback: (value) => {
          if (value === 0) {
            return 'Tidak'
          } else if (value === 1) {
            return 'Iya'
          } else {
            return ''
          }
        }
      },
      SP: {
        field: 'SP',
        callback: (value) => {
          if (value === 0) {
            return 'Tidak'
          } else if (value === 1) {
            return 'Iya'
          } else {
            return ''
          }
        }
      }
    }
  }),
  mounted: function () {
    store.dispatch('hccomben/reqData')
  },
  computed: {
    ...mapState('hccomben', {
      data: (state) => state
    })
  },
  methods: {
    ...mapActions('hccomben', [
      'detail', 'edit'
    ]),
    yesOrNo (item) {
      if (item === 1) {
        return 'Ya'
      } else if (item === 0) {
        return 'Tidak'
      } else {
        return '-'
      }
    },
    toRupiah (nominal) {
      return Utils.toRupiahString(nominal)
    },
    toDateStringMount (tanggal) {
      return Utils.toDateStringMount(tanggal)
    }
  }
}
</script>
